.container{
    max-width: 1200px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    gap: 30px;
}
.icons{
    display: flex;
    gap: 20px;
    padding: 20px 0;
    border-top: 3px dotted #eee;
}
.text{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.text h2{
    font-size: clamp(20px, 3vw, 40px) ;
    font-weight: 700;
    color: #2a2a2a;
}
.text p{
    font-size: clamp(11px, 1vw, 14px);
    color: #a1a1a1;
    font-weight: 400;
}
.text span{
    font-size: clamp(11px, 1vw, 14px);

}
@media only screen and (max-width: 768px){
    .container{
        flex-direction: column;
        justify-content: center;
    }
    .text{
        width: 100% !important;
    }
}