.container{
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    gap: 30px;
}
.field{
    width: 100%;
    margin-top: 30px;
}
.field div{
    display: flex;
    justify-content: space-between;
}
.field div input{
    width: 48%;
}
textarea{
    width: 100%;
    margin-top: 20px;
    border: 1px solid #2a2a2a;
    padding: 10px 15px;
}
textarea:focus{
    outline: none;
}
.field button{
    width: 45px;
    height: 45px;
    background: #2a2a2a;
    display: flex;
    justify-content: center;
    align-items: center;
    border: #2a2a2a 1px solid;
    margin-top: 20px;

}
.field button:hover{
    background: white;
}
.field button:hover svg{
    fill: #2a2a2a !important;
}
.map{
    width: 50%;
}
@media only screen and (max-width: 768px){
    .container{
        flex-direction: column;
    }
    .map{
        width: 100%;
    }
    .field div{
        flex-direction: column;
        gap: 20px;
    }
    .field div input{
        width: 100%;
    }
    .field button{
        width: 100%;
    }
}