.container{
    max-width: 1200px;
    margin: 0 auto;
}
.products{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    justify-content: space-between;
    gap: 30px 20px;
    margin-top: 75px;
}
.productTitle h6{
    font-size: 18px;
    color: #a1a1a1;
    font-weight: 500;
    display: block;
}
.row{
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: -60px;
    left: 50%;
    transform: translateX(-50%);
}
.image{
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
}
.image:hover .row{
    bottom: 10%;
}
.row div{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    cursor: pointer;
}
.pageination{
    width: 250px;
    margin: 75px auto;
    display: flex;
    justify-content: space-between;
    gap: 10px;
}
.page{
    width: 40px;
    border: #2a2a2a 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
}
.page:hover{
    background: #2a2a2a;
    color: white;
}
.page:hover svg{
    fill: white;

}
@media only screen and (max-width: 768px) {
    .products {
        grid-template-columns: 100%;
        gap: 100px 0;
    }
    .productTitle h4{
        font-size: 20px;
        font-weight: bold;
    }
}