.row{
    display: grid;
    grid-template-columns: 16.6% 16.6% 16.6% 16.6% 16.6% 16.6%;
    margin-top: 50px;
}
.image img{
    width: 100%;
}
.image{
    position: relative;
    overflow: hidden;
}
.hidden{
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    background: rgba(42, 42, 42, .97);
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding: 20px;
    opacity: 0;
    visibility: hidden;
}
.hidden h6{
    color: white;
}
.image:hover .hidden{
    opacity: 1;
    visibility: visible;
}
@media only screen and (max-width: 768px){
    .row{
        grid-template-columns: 100%;
        gap: 10px;
    }
}