.banner{
    width: 100%;
    background-image: url("../../../img/product/products-page-heading.jpg");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 400px;
}
.container{
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}
.banner .container h2{
    font-size: clamp(25px, 4vw, 42px);
    color: #fff;
    font-weight: 700;
    margin-bottom: 15px;
}
.banner .container p{
    font-size: clamp(8px, 3vw, 16px);
    font-weight: 500;
    color: #fff;
    font-style: italic;
}