.container{
    max-width: 1200px;
    margin: 75px auto;
    display: flex;
    justify-content: space-between;
    gap: 15px;
}
.cardTitle{
    text-align: center;
    margin-top: 30px;
}
.cardTitle h3{
    font-size: clamp(15px, 4vw, 25px);
    color: #2a2a2a;
    font-weight: 700;
    margin-bottom: 5px;
}
.image{
    position: relative;
}
.image img{
    width: 100%;
}
.imageHover{
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    background: rgba(42, 42, 42, .97);
    opacity: 0;
}
.imageHover:hover{
    opacity: 1;
}
@media only screen and (max-width: 768px) {
    .container {
        flex-direction: column;
        justify-content: center;
    }
}