.productTitle h4{
    font-size: clamp(10px, 2vw, 22px);
    color: #2a2a2a;
    font-weight: 700;
    margin-bottom: 8px;
}
.productTitle h6{
    font-size: 18px;
    color: #a1a1a1;
    font-weight: 500;
    display: block;
}
.row{
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: -60px;
    left: 50%;
    transform: translateX(-50%);
}
.image{
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
}
.image:hover .row{
    bottom: 10%;
}
.row div{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    cursor: pointer;
}