header{
    border-bottom: 3px dotted #eee;
    padding: 20px 100px;
    position: sticky;
    top: 0;
    z-index: 1000;
    background: white;
}
.headerContainer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 20px;
}
.navigation{
    display: flex;
    flex-direction: row;
    gap: 0 32px;
}
.navigation li{

    list-style: none;
}
.navigation li:hover a{
    color: #aaa;
}
.navigation li a{
    color: #2a2a2a;
    font-weight: 600;
    font-size: 15px;
    text-decoration: none;
}
.menu{
    position: absolute;
    bottom: -230px;
    background: #f7f7f7;
    visibility: hidden;
    opacity: 0;
    width: 150px;
    min-height: 200px;
}
.menu li{
    height: 50px;
    border-bottom: 1px solid #d9d9d9;
    display: flex;
    align-items: center;
    padding: 0 10px;
    cursor: pointer;
}
.menu li a{
    font-size: 13px;
    color: #2a2a2a !important;
    font-weight: 400;
}
.logo{
    width: 130px;
}
.logo img{
    width: 100%;
}
.menu li:hover{
    background: white;
    padding: 0 15px;
}
.select{
    position: relative;
}
.select:hover .menu{
    visibility: visible;
    opacity: 1;
    bottom: -210px;
}
.select a{
    display: flex;
    align-items: center;
}
.menuBar{
    width: 22px;
    height: 22px;
    display: none;
}
.menuBar div{
    width: 100%;
    height: 2px;
    background: #2a2a2a;
}
.mobileNavigation{
    background: #f7f7f7;
    position: absolute;
    z-index: 100;
    width: 60%;
    border-bottom: 1px solid #d9d9d9;
    font-size: 13px;
    padding: 0 10px;
    color: #2a2a2a;
    left: 50%;
    top: 80px;
    transform: translateX(-50%);
    height: 0;
    visibility: hidden;

}
.mobileNavigation li{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d9d9d9;

}
@media only screen and (max-width: 768px){

    .navigation{
        display: none;
    }
    .menuBar{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}
