.container{
    display: flex;
    justify-content: space-between;
    gap: 30px;
}
.text{
    width: 44%;
}
.text h2{
    font-size: 34px;
    font-weight: 700;
    color: #2a2a2a;
}
p{
    font-size: 14px;
    color: #aaa;
    font-weight: 500;
}
.text div{
    display: flex;
    gap: 10px;
    align-items: center;
    margin-top: 25px;

}
.text div span{
    color: #2a2a2a;
    font-weight: 500;
}
.text button{
    color: #2a2a2a;
    border: 1px solid #2a2a2a;
    margin-top: 35px;
    font-size: 13px;
    width: 140px;
    height: 45px;
}
.text button:hover{
    background: #2a2a2a;
    color: white;
}
.images{
    width: 44%;
    display: grid;
    grid-template-columns: 48% 48%;
    gap: 20px;
    height: max-content;
}
.images img{
    width: 100%;
    height: 100%;
}
.images div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 20px;
    background-color: #f8f8f8;
    
}
.images div h3{
    font-size: 24px;
    font-weight: 700;
    color: #2a2a2a;
    margin-bottom: 5px;
}
.images div span{
    font-size: 14px;
    color: #aaa;
    font-weight: 500;
}
@media only screen and (max-width: 768px){
    .container{
        flex-direction: column;
        align-items: center;
    }
    .images{
        width: 100%;
        grid-template-columns: 100%;
    }
    .images img{
        width: 100%;
        height: auto;
    }
}