footer{
    background: #2a2a2a;
    padding: 50px 100px
}
.container{
    display: flex;
    justify-content: space-between;
    gap: 50px 20px;
    flex-wrap: wrap;
}
footer .container li {
    color: white;
    margin-top: 10px;
}
footer .container h3{
    color: white;
    font-weight: 700;
    font-size: 18px;
}
footer .container li a:hover{
    color: #aaa;
}
.contactInfo{
    width: 25%;
}
@media only screen and (max-width: 768px){
    .container{
        flex-direction: column;
    }
    .container div{
        width: 100%;
    }
}