.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
}
.field{
    width: 60%;
}
form{
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
}
input{
    border: 1px solid #2a2a2a;
    width: 42%;
    height: 45px;
    padding: 0 15px;
}

::placeholder{
    font-size: 15px;
    font-style: italic;
}
input:focus{
    outline: none;
}
form button{
    width: 45px;
    height: 45px;
    background: #2a2a2a;
    display: flex;
    justify-content: center;
    align-items: center;
    border: #2a2a2a 1px solid;
}
form button:hover{
    background: white;
}
form button:hover svg{
    fill: #2a2a2a !important;
}
.contactInfo{
    width: 40%;
    display: grid;
    grid-template-columns: 150px 150px;
    gap: 20px 10px;
    justify-content: space-between;
}
.contactInfo h3{
    font-size: 16px;
    font-weight: 700;
    color: #2a2a2a;
}
@media only screen and (max-width: 768px){
    .container{
        flex-direction: column;
    }
    .field{
        width: 100%;
    }
    form{
        flex-direction: column;
    }
    input{
        width: 100%;
    }
    form button{
        width: 100%;
    }
    .contactInfo{
        width: 100%;
        grid-template-columns: 40% 40%;
    }
}