.bannerContainer{
    display: flex;
    justify-content: space-between;
    gap: 2%;
}
.leftBanner{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    position: relative;
    height: 100%;
}
.bannerText{
    padding: 20px;
    color: white;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}
.bannerText p{
    margin-bottom: 20px;
}

.bannerText button{
    padding: 12px 23px;
    background: transparent;
    border: 2px white solid;
    color: white;
    font-size: 14px;
    cursor: pointer;
    font-weight: 600;
}


.leftBanner h1{
    font-size: clamp(35px, 8vh, 72px);
    color: white;
}

.rightBanner{
    display: grid;
    grid-template-columns: 48% 48%;
    width: 50%;
    text-align: center;
    gap: 4.5%;
    height: 100%;
}
.image{
    position: relative;
}
.image img{
    width: 100%;
    height: 100%;
}
.hoverText{
    position: absolute;
    width: 85%;
    height: 85%;
    background: rgba(42, 42, 42, .97);
    opacity: 0;
    visibility: hidden;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 10px;
    cursor: pointer;
    transition: 1s ease;
}
.hoverText h2{
    font-size: clamp(10px, 2vw, 22px);
}
.hoverText p{
    margin-bottom: 20px;
    font-size: 10px;
}
.hoverText button{
    width: 120px;
    height: 50px;
    background: transparent ;
    border: 2px white solid;
    color: white;
    font-size: 12px;
    cursor: pointer;
    font-weight: 600;
    padding: 0 10px;
}
.bannerText button:hover{
    background: white;
    color: #2a2a2a;
}
.hoverText button:hover{
    background: white;
    color: #2a2a2a;
}
.image:hover .hoverText{
    background: rgba(42, 42, 42, .97);
    transition: 1s;
    opacity: 1;
    visibility: visible;
}
@media only screen and (max-width: 958px){
    .bannerContainer{
        flex-direction: column;
        gap: 30px 0;
    }
    .leftBanner{
        width: 100%;
    }
    .rightBanner{
        width: 100%;
    }
}
@media only screen and (max-width: 768px){
    .rightBanner{
        grid-template-columns: 100%;
        gap: 30px;
    }
    .leftBanner h1{
        font-size: clamp(15px, 4vw, 35px);
    }
}
@media only screen and (max-width: 480px){
    .bannerContainer p{
        font-size: 10px;
    }
    .leftBanner h1{
        font-size: 14px;
    }

}