section{
    border-bottom: 3px dotted #eee;
    padding: 50px 100px;
}
.title h2{
    font-size: clamp(20px, 3vw, 40px) ;
    font-weight: 700;
    color: #2a2a2a;
}
.title p{
    font-size: clamp(11px, 1vw, 14px);
    color: #a1a1a1;
    font-weight: 400;
}
.title span{
    font-size: clamp(11px, 1vw, 14px);

}
@media only screen and (max-width: 768px){
    section{
        padding: 50px 20px;
    }
    header{
        padding: 20px 50px !important;
    }
}
