.container{
    max-width: 1200px;
    margin: 75px auto;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.card{
    padding: 30px;
    box-shadow: 0 0 15px rgba(0, 0, 0, .1);
    text-align: center;
}
.card h4{
    font-size: clamp(15px, 8vw, 24px);
    font-weight: 700;
    color: #2a2a2a;
    margin-bottom: 20px;
}
.card p{
    font-size: clamp(10px, 4vw, 14px);
    line-height: 25px;
    color: #2a2a2a;
    margin-bottom: 20px;

}
.card img{
    width: 100%;
}
@media only screen and (max-width: 768px) {
    .container {
        flex-direction: column;
        justify-content: center;
    }
    .card{
        width: 100%;
    }
}