.container{
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 65% 35%;
    gap: 20px ;
}
.top{
    padding-bottom: 15px;
    border-bottom: 1px solid #eee;
}
.top h3{
    font-size: 22px;
    color: #2a2a2a;
    font-weight: 700;
}
.top p{
    font-size: 18px;
}
.text{
    padding: 15px 0;
}
h4{
    font-size:  clamp(10px, 2vw, 20px);
    font-weight: 700;
    color: #aaa;
}
.quantity{
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    border-bottom: 1px solid #eee;
    border-top: 1px solid #eee;
}
.counter{
    display: flex;
    align-items: center;
    gap: 10px;
}
.counter button{
    width: 30px;
    height: 30px;
    color: #2a2a2a;
    border: 1px solid #2a2a2a;
}
.counter button:hover{
    background: #2a2a2a;
    color: white;
}
.counter div{
    width: 30px;
    height: 30px;
    border: 1px solid #2a2a2a;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #2a2a2a;
}
.addToCart{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: max-content;
}
.addToCart button{
    border: 1px solid #2a2a2a;
    color: #2a2a2a;
    padding: 5px 10px ;
    font-size: 14px;
}
.addToCart button:hover{
    background: #2a2a2a;
    color: white;

}
@media only screen and (max-width: 768px){
    .container{
        grid-template-columns: 100%; 
    }
}